* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.my-edit{
  border-radius: 18px;
  box-shadow: 0 4px 5px grey;
}

.go {
  width: 100%;
}

.come {
  width: 25% !important;
}

.add {
